.tableContainer {
  margin-top: 25px;
}

.textTable {
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2px;
}
.navbar {
    background-color: #060b26;
    
    display: flex;
    
    
    align-items: flex-start !important;
    height: 100%;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: left;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;    
    padding: 0px 64px 0px 0px;
}

span {
    margin-left: 16px;
}
.simplekeyboard {
    margin-top: 64px;
    margin-left: auto;
    margin-right: auto;
    max-width: 850px;
}

.simple-keyboard.myTheme1 {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 0;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  
  .simple-keyboard.myTheme1 .hg-button {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    color: white;
  }
  
  .simple-keyboard.myTheme1 .hg-button:active {
    background: #1c4995;
    color: white;
  }
  
  #root .simple-keyboard.myTheme1 + .simple-keyboard-preview {
    background: #1c4995;
  }
  
  .input {
    background-color: #f7f4f4;
    margin-left: auto;
    margin-right: auto;
    max-width: 850px;
    width: 100%;
    height: 0px;
    font-size: 20px;
    border: none;
    padding-left: 8px;
    padding-right: 8px;
  }
.letter {
  font-family: "Montserrat", sans-serif;
  margin-left: 0;
  margin-right: 0;
  font-size: x-large;
  letter-spacing: 2px;
}

.completed {
  color: rgb(136, 136, 136);
  background-color: rgba(0, 128, 0, 0.068);
}

.current {
  background-color: black;
  color: white;
  animation: 1s blink step-start infinite;
}

@keyframes blink {
  from,
  to {
    background-color: transparent;
    color: black;
  }
  50% {
    background-color: black;
    color: white;
  }
}

.wrong {
  color: rgb(224, 11, 11);
  background-color: rgba(255, 0, 0, 0.479);
}
.indicatorRow {
  font-size: x-large;
}
.speed {
  color: green;
  margin-left: 0;
}
.mistakes {
  color: red;
}
.writingArea {
  height: 100px;
  padding-top: 24px;
  background-color: rgb(240, 240, 240);
}
.container {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.activeWord {
  color: rgb(109, 109, 109);
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 6px;
  margin-left: auto;
  margin-right: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.indicatorRow {
  font-size: x-large;
  padding-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.sorted {
  color: green;
  padding-right: 50px;
}
.skipped {
    color: rgb(255, 102, 0);
}
.refused {
    padding-left: 50px;
    color: red;
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.column {
    flex: 1 1;
}

.buttonSkip {
    margin-left: 100px;
}

