.simplekeyboard {
    margin-top: 64px;
    margin-left: auto;
    margin-right: auto;
    max-width: 850px;
}

.simple-keyboard.myTheme1 {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 0;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  
  .simple-keyboard.myTheme1 .hg-button {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    color: white;
  }
  
  .simple-keyboard.myTheme1 .hg-button:active {
    background: #1c4995;
    color: white;
  }
  
  #root .simple-keyboard.myTheme1 + .simple-keyboard-preview {
    background: #1c4995;
  }
  
  .input {
    background-color: #f7f4f4;
    margin-left: auto;
    margin-right: auto;
    max-width: 850px;
    width: 100%;
    height: 0px;
    font-size: 20px;
    border: none;
    padding-left: 8px;
    padding-right: 8px;
  }