@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

.Body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body {
  overflow-x: auto;
  overflow-y: scroll;
  color: #26243d;
  background-color: #f5f0f0;
}

body, html {
  margin: 0;
  padding: 0;
}

.Body-footer, .Body-header {
  flex: 0 0 auto;
  margin: 0;
  padding: 1rem;
  background-color: #060b26;
  color: #f5f5f5 !important;
  text-align: center;
}

.Body-header {
  border-bottom: 1px solid  #f5f5f5;
}

.Body-footer {
  border-top: 1px solid #f5f5f5;
}

.Body-container {
  display: flex;
  flex: 1 0 auto;
}

.Body-aside {
  flex: 0 0 auto;
  padding: 1rem;
  border-right: 1px solid #f5f5f5;
  background-color: #060b26;
  color: #f5f5f5 !important;
}

*, :after, :before {
  box-sizing: border-box;
}

.Body-app {
  position: relative;
  flex: 1;
}

.Body-nav {
  border-left: 1px solid rgba(0,10,20,.1);
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}



