.activeWord {
  color: rgb(109, 109, 109);
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 6px;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
}

.indicatorRow {
  font-size: x-large;
  padding-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
}

.sorted {
  color: green;
  padding-right: 50px;
}
.skipped {
    color: rgb(255, 102, 0);
}
.refused {
    padding-left: 50px;
    color: red;
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.column {
    flex: 1;
}

.buttonSkip {
    margin-left: 100px;
}
